/* Header Outer Container - Background & Positioning */
.header_outer_container {
    background-color: #141414;
    padding: 10px 0;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;
    transition: background-color 0.3s;
}

/* Header Container - Alignment and Spacing */
.header_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 30px;
}

/* Header Left - Logo and Navigation Links */
.header_left ul {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    align-items: center;
}

.header_left ul li {
    color: #e5e5e5;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: color 0.2s;
    margin-right: 20px;
}

.header_left img {
    width: 100px;
    margin-right: 20px;
}

/* Hover effect for nav items */
.header_left ul li:hover {
    color: #b3b3b3;
}

/* Header Right - Icon Styling */
.header_right ul {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    align-items: center;
}

.header_right ul li {
    display: flex;
    align-items: center;
    color: #e5e5e5;
    margin-left: 15px;
    font-size: 18px;
    cursor: pointer;
    transition: color 0.2s;
}

/* Hover effect for icons */
.header_right ul li:hover {
    color: #b3b3b3;
}

.header_right ul li svg {
    font-size: 24px;
}

/* Responsive adjustments */
@media (max-width: 1024px) {
    .header_container {
        padding: 0 20px;
    }
}

@media (max-width: 768px) {

    /* Hide some menu items on smaller screens */
    .header_left ul li:nth-child(n+3) {
        display: none;
    }

    /* Adjust icon spacing */
    .header_right ul li {
        margin-left: 10px;
    }
}

@media (max-width: 480px) {

    /* Reduce logo size for mobile */
    .header_left img {
        width: 80px;
    }

    /* Adjust font size on small screens */
    .header_left ul li {
        font-size: 12px;
    }

    .header_right ul li svg {
        font-size: 20px;
    }
}