.footer_outer_container {
    background-color: #111;
    color: grey;
    display: flex;
    justify-content: center;
    /* margin-top: 70px; */
}

.footer_inner_container {
    width: 60%;
    /* background-color: red; */
}

.footer_icons {
    /* background-color: blue; */
    width: 12%;
    display: flex;
    justify-content: space-between;
    margin-left: 10px;
    color: #fff;
}

.footer_data {
    display: flex;
    justify-content: space-between;
}

.footer_data ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 12vh;
    /* background-color: blue; */
}

.footer_data ul li {
    cursor: pointer;
}

.service_code {
    display: flex;
    align-items: center;
    justify-content: center;
    border: #fff 1px solid;
    width: 10%;
    margin: 10px 0;
    cursor: pointer;
}

.copy-write {
    font-size: 12px;
    margin-bottom: 10px;
}

@media (max-width: 768px) {
    .footer_inner_container {
        display: block;
        width: 100%;
        /* Full width for mobile */
        padding: 0 10px;
        /* Add padding to the sides */
    }

    .footer_icons {
        width: 100%;
        /* Full width for mobile */
        justify-content: center;
        margin-bottom: 15px;
    }


    .footer_inner_container {
        flex-direction: column;
        align-items: left;
    }

    .footer_data {
        flex-direction: column !important;
        /* Show links in a row on mobile */
        justify-content: space-around;
        align-items: left;
        padding-left: 15px;

        height: auto;
    }

    .footer_data ul {
        padding-bottom: 20px;

    }

    .footer_icons {
        margin-left: -260px;

    }

    .service_code {
        margin-left: -260px;
    }

    .copy-write {
        margin-left: -260px;
    }

    .service_code,
    .copy-write {
        text-align: center;
        width: 100%;
        /* Center-align and full width */
    }          
        
    
}